.about {
    height: 580px;
    position: relative;
    background-color:var(--color-purple);

    @media (max-width: 540px) {
            height: auto;
    }
    &__container {
        display: flex;
        flex-wrap: wrap;
        overflow: hidden;
        position: relative;
        height: 100%;
    }
    &__circle {
        position: absolute;
        width: 550px;
        height: 424px;
    }
    &__text {
        font: var(--font-book-16);
        color: #fff;
        max-width: 380px;
        transform: translateY(39%);
        @media (max-width: 768px) {
            transform: translateY(25%);
        }
        @media (max-width: 768px) {
            max-width: 239px;
        }
        @media (max-width: 540px) {
            max-width: 100%;
            transform: none;
            padding: var(--big-offset) 0;
        }

    }
    &__title {
        color: #fff;
        margin: 0;
        margin-bottom: var(--second-offset);
    }
    &__author {
        text-align: right;
        font: var(--font-bold-20);
        margin: 0;
        margin-top: var(--second-offset);
        position: relative;
        transform: translateX(65px);
        @media (max-width: 920px) {
            transform: translateX(0);

        }
        &:before {
            content: '';
            position: absolute;
            height: 3px;
            width: 30px;
            left: 62%;
            @include center-y;
            background-color: var(--color-white);
            @media (max-width: 768px) {
                left: 42%;
            }
        }
    }
    &__img {
        width: 500px;
        height: 950px;
        transform: rotate(15deg);
        overflow: hidden;
        position: absolute;
        right: 100px;
        top: -150px;
        flex-shrink: 0;
        background-color: red;
        @media (max-width: 1110px) {
            width: 482px;
            height: 915px;
            right: -65px;
        }
        @media (max-width: 850px) {
            right: -145px;
        }
        @media (max-width: 645px) {
                width: 405px;
                height: 740px;
                top: 7px;
        }
        @media (max-width: 540px) {
                display: none;
        }
        img {
            transform: rotate(-15deg);
            width: 135%;
            height: 100%;
            left: -80px;
            max-width: none;
            object-fit: cover;
            position: absolute;
            @media (max-width: 765px) {
                height: 82%;
                left: -109px;
            }
            @media (max-width: 645px) {
                width: 400px;
                height: 594px;
                top: 19px;
            }
        }
    }
}
